:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

ul {
  list-style: none;
}

p {
  margin: 0;
}

body {
  background-color: gray;
}

.list {
  max-width: 1140px;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  grid-auto-rows: 120px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  display: grid;
}

.empty_container {
  z-index: 200;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.word_item {
  cursor: pointer;
  text-align: center;
  background-color: #f4f4fd;
  border-radius: 5px;
  padding: 20px 24px;
  font-size: x-large;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: relative;
  transform: scale(1);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.word_item:hover {
  transform: scale(1.03);
}

.is_hidden p {
  display: none;
}

.type-text {
  color: #00f;
  margin-top: 10px;
}

